.card {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #fff;
  border-radius: 15px;
  padding: 10px;
}

.card__back {
  width: 100%;
  height: 100%;
  background-color: #6d695c;
  background-image: repeating-linear-gradient(
      120deg,
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.1) 1px,
      transparent 1px,
      transparent 60px
    ),
    repeating-linear-gradient(
      60deg,
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.1) 1px,
      transparent 1px,
      transparent 60px
    ),
    linear-gradient(
      60deg,
      rgba(0, 0, 0, 0.1) 25%,
      transparent 25%,
      transparent 75%,
      rgba(0, 0, 0, 0.1) 75%,
      rgba(0, 0, 0, 0.1)
    ),
    linear-gradient(
      120deg,
      rgba(0, 0, 0, 0.1) 25%,
      transparent 25%,
      transparent 75%,
      rgba(0, 0, 0, 0.1) 75%,
      rgba(0, 0, 0, 0.1)
    );
  background-size: 70px 120px;
  border-radius: 15px;
}

.card__rule {
  height: 70px;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  text-align: center;
  font-size: 24px;
  padding: 0 10px;
}

.card__mark {
  font-size: 48px;
  height: 10%;
  display: inline-flex;
  padding: 0 5px;
  justify-content: center;
  align-items: center;
}

.card__mark--revert {
  transform: rotateZ(180deg);
  position: absolute;
  bottom: 10px;
  right: 10px;
}
