.app {
  background-color: #282c34;
  width: 100%;
  height: 100%;
}

.app__reload {
  border: none;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  font-size: 32px;
  position: absolute;
  outline: none;
}

.app__deck {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.app__card {
  width: 90%;
  height: 0;
  padding-bottom: 140%;
  position: absolute;
}
